.skills {
    height: 80vh;
    width: 100vw;
    max-width: 85vw;
    margin: 0 auto;
}

.head{
    height: 10rem;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.head h1{
    font-size: 3rem;
    margin: 1rem;
    color: yellow;
}

.head p {
    font-size: 1rem;
    text-align: center;
    margin: 1rem;
}

.detail {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.detail div {
    height: 15rem;
    width: 14rem;
    margin: 0 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.detail div h2 {
    margin: 10px;
    color: rgb(135, 135, 125);
}

.detail div img {
    height: 4rem;
    width: 4rem;
}