.nav {
    height: 5rem;
    width: 100vw;
    max-width: 85vw;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: rgb(30, 30, 30);
}

.logo{
    object-fit: cover;
    height: 3rem;
    width: 4rem;
}

.menuLinkItem {
    margin: 1rem;
    cursor: pointer;
}

.menuLinkItem:hover {
    color: yellow;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid yellow;
}

.msgLogo {
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem 0.5rem;
}

.menuBtn {
    cursor: pointer;
    background-color: white;
    color: black;
    padding: 0 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 2rem;
}

.menuBtn:hover {
    background-color: yellow;
}