.roject {
    height: 90vh;
    width: 100vw;
    max-width: 85vw;
    margin: 5rem auto;
}

.project h1 {
    text-align: center;
    margin: 9rem 0 5rem 0;
    font-size: 5rem;
    color: yellow;
}

.project .box img {
    height: 6rem;
}

.project .box {
    margin: 0 0 5rem 10rem;
    display: flex;
}

.project .box div {
    display: flex;
    margin-left: 10px;
    flex-direction: column;
    justify-content: center;
}

.project .box div h2 {
    color: rgb(135, 135, 125);
}
