.intro {
    height: calc(100vh - 5rem);
    width: 100vw;
    max-width: 85vw;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}
.introImg{
    width: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
}
.introContent{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 500;
}
.hello{
    font-size: 1.75rem;
    font-weight: 100;
}
.name{
    color: yellow;
}
.introPara{
    font-size: medium;
    font-weight: 100;
}
.btnImg{
    height: 1.5rem;
    object-fit: cover;
    margin: 0 0.5rem;
}   
.btn {
    background-color: white;
    color: black;
    border-radius: 2rem;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.5rem 1rem;
    margin: 2rem 0;
    cursor: pointer;
    font-weight: 600;
}
