.contact {
    height: 70vh;
    width: 100vw;
    max-width: 85vw;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.contact form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.contact input, textarea {
    height: 3rem;
    width: 30rem;
    border: none;
    background: #373333;
    margin: 1rem 0;
    padding-left: 1rem;
    border-radius: 15px;
    color: white;
}

.contact textarea {
    height: 5rem;
    padding-top: 1rem;
}

.contact h1 {
    margin: 1rem;
    font-size: 4rem;
    color: yellow;
}

.contact button {
    height: 3rem;
    width: 7rem;
    background: white;
    font-weight: 900;
    border: none;
    border-radius: 20px;
    cursor: pointer;
}